import React, { Component } from 'react';
import crowdfunding from "../Crowdfunding"; //   smart contract (Crowdfunding)
import web3 from '../web3'; //   web3      blockchain
import '../CreateCampaignForm.css'; //   CSS  

class CreateCampaignForm extends Component {
  state = {
    title: '', //   
    pledgeCost: '', //  pledge ()
    pledgesNeeded: '', //   pledges
    message: '', //      (..   )
    isOwner: false, //          
    isBanned: false, //       banned
    currentAccount: '', //    
  };

  async componentDidMount() {
    try {
      const owner = await crowdfunding.methods.owner().call(); //        
      const accounts = await web3.eth.getAccounts(); //       Metamask
      const currentAccount = accounts[0]; //       

      const isBanned = await crowdfunding.methods.bannedEntrepreneurs(currentAccount).call(); 
      //      banned (  mapping   )

      this.setState({
        isOwner: owner.toLowerCase() === currentAccount.toLowerCase(), //    currentAccount   owner
        isBanned, //       banned
        currentAccount, //     
      });

      //       Metamask
      if (window.ethereum) {
        window.ethereum.on('accountsChanged', async (accounts) => {
          const updatedAccount = accounts[0]; //   
          const updatedIsBanned = await crowdfunding.methods.bannedEntrepreneurs(updatedAccount).call(); 
          //       banned

          this.setState({
            currentAccount: updatedAccount, //    
            isOwner: owner.toLowerCase() === updatedAccount.toLowerCase(), //    owner
            isBanned: updatedIsBanned, //    banned
          });
        });
      }
    } catch (error) {
      console.error('Error loading owner or account:', error); //      owner   
    }
  }

  onSubmit = async (event) => {
    event.preventDefault(); //      (refresh)

    const { title, pledgeCost, pledgesNeeded } = this.state;

    this.setState({ message: 'Transaction is being processed...' }); //      

    try {
      const accounts = await web3.eth.getAccounts(); //     Metamask
      const pledgeCostInWei = web3.utils.toWei(pledgeCost, 'ether'); //   pledge cost  ETH  Wei
      const valueInWei = web3.utils.toWei('0.02', 'ether'); //   

      await crowdfunding.methods.createCampaign(title, pledgeCostInWei, pledgesNeeded).send({
        from: accounts[0], //   
        value: valueInWei, //  
      });

      this.setState({ message: 'Campaign successfully created!' }); //   
    } catch (err) {
      console.error('Error creating campaign:', err); //     
      this.setState({ message: 'There was an error creating the campaign.' }); //   
    }
  };

  render() {
    const { title, pledgeCost, pledgesNeeded, message, isOwner, isBanned } = this.state;

    const isDisabled = isOwner || isBanned; //         owner  banned

    return (
      <div className="create-campaign-container">
        <h3 className="form-title">New Campaign</h3>
        <form onSubmit={this.onSubmit} className="create-campaign-form">
          <div className="form-group">
            <label className="form-label">Title:</label>
            <input
              className="form-input"
              value={title} //   
              onChange={(event) => this.setState({ title: event.target.value })} //   state    
              placeholder="Enter the Campaign Title" // Placeholder  
              disabled={isDisabled} //      owner  banned
            />
          </div>
          <div className="form-group">
            <label className="form-label">Pledge cost:</label>
            <input
              type="text"
              className="form-input"
              value={pledgeCost} //   pledge cost
              onChange={(event) => this.setState({ pledgeCost: event.target.value })} //   state    
              placeholder="Enter pledge cost in ETH" // Placeholder  
              disabled={isDisabled} //      owner  banned
            />
          </div>
          <div className="form-group">
            <label className="form-label">Number of pledges:</label>
            <input
              type="text"
              className="form-input"
              value={pledgesNeeded} //    pledges
              onChange={(event) => this.setState({ pledgesNeeded: event.target.value })} //   state    
              placeholder="Enter number of pledges" // Placeholder  
              disabled={isDisabled} //      owner  banned
            />
          </div>
          <button
            type="submit"
            className={`form-button ${isDisabled ? 'disabled' : ''}`} //  CSS class      disabled
            disabled={isDisabled} //      owner  banned
          >
            Create
          </button>
        </form>
        <p className="form-message">{message}</p> {/*   */}
      </div>
    );
  }
}

export default CreateCampaignForm; //   component
